<template>
  <div>
    <h1>form입력 데이터 바인딩</h1>
    <pre style="text-align: left">
      pre 태그는 줄바꿈/띄어쓰기를 그대로 적용해준다.

      씨를 뿌리는 자가 뿌리러 나가서 
      더러는 흙이 얕은 돌밭에 떨어지매 
      흙이 깊지 아니하므로 곧 싹이 나오나 
    </pre>
    <input type="text" v-model="scenario" placeholder="input text" />
    <p>This is : {{ scenario }}</p>
    <hr />
    <h2>multi line messgae</h2>
    <textarea
      cols="30"
      rows="10"
      v-model="textareaFullTxt"
      placeholder="input"
    ></textarea>
    <div>
      <label> <input type="checkbox" v-model="checked" />{{ checked }} </label>
    </div>
    <hr />
    <h2>선택한 이름{{ selectNames }}</h2>
    <input type="checkbox" id="jack" value="Jack" v-model="selectNames" />
    <label for="jack">Jack Hue</label>
    <input type="checkbox" id="michel" value="Michel" v-model="selectNames" />
    <label for="michel">Michel Jordon</label>
    <input type="checkbox" id="jushua" value="Jushua" v-model="selectNames" />
    <label for="jushua">Jushua Verman</label>
    <hr />
    <div>
      <label for="">
        <input type="radio" value="서울" v-model="picked" />서울
      </label>
      <label for="">
        <input type="radio" value="안산" v-model="picked" />안산
      </label>
      <label for="">
        <input type="radio" value="아컴" v-model="picked" />아컴
      </label>
      <br />
      <span>선택한 지역 : {{ picked }}</span>
    </div>

    <div>selected : {{ selected }}</div>
    <select v-model="selected">
      <option disabled value="다음 중 하나를 선택">다음 중 하나를 선택</option>
      <option value="02">서울</option>
      <option value="031">경기도</option>
      <option value="064">부산</option>
      <option value="032">인천</option>
    </select>
    <h1>수식어</h1>
    <p>.number : 사용자 입력 > 숫자로 변경</p>
    <input type="number" v-model.number="inputNum" />숫자 입력하세요
    <p>{{ inputNum }}</p>
    <br />
    <hr />
    <h2>true-value 와 false-value</h2>
    <input type="checkbox" v-model="toggle" true-value="KP" false-value="PL">{{ toggle }}
  </div>
</template>

<script>
export default {
  data() {
    return {
      scenario: "유산",
      textareaFullTxt:
        "씨를 뿌리는 자가 뿌리러 나가서 더러는 흙이 얕은 돌밭에 떨어지매 흙이 깊지 아니하므로 곧 싹이 나오나 해가 돋은 후에 타서 뿌리가 없으므로 말랐고 더러는 가시떨기 위에 떨어지매 가시가 자라서 기운을 막았고 더러는 좋은 땅에 떨어지매 어떤 것은 백 배, 어떤 것은 육십 배, 어떤 것은 삼십 배의 결실을 하였느니라",
      checked: false,
      selectNames: [],
      picked: "",
      selected: "다음 중 하나를 선택",
      inputNum: 0,
      toggle:"PL",
    };
  },
};
</script>

<style scoped>
* {
  text-align: left;
}
</style>
